@import url(https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Noto Sans', sans-serif;
}

.navbar {
  background-color: #fff !important;
}

.nav_bg {
  background-color: #fff !important;
}

.navbar-brand {
  font-size: 1.9rem !important;
  color: #ff5203 !important;
}

.nav-item-book {
  font-size: 1.3rem !important;
  color: #ff5203 !important;
  padding-top: 14px;
  font-weight: 600;
}

.itmcenter {
  text-align: right;
}



.navbar a {
  font-size: 1.3rem;
  text-transform: inherit;
}

.navbar_active {
  font-weight: bold;
  border-bottom: 1px solid #ff5203;
}

.navbar a:hover {
  color: #c24e19 !important;

}




@media (max-width: 991px) {
  .navbar-brand {
    font-size: 1.5rem !important;
  }

  .navbar_active {
    font-weight: bold;
    border-bottom: none;
  }
}


@media (max-width: 778) {
  .navbar-brand {
    font-size: 1rem !important;
  }

  .navbar_active {
    font-weight: bold;
    border-bottom: none;
  }
}



@media (max-width: 480) {
  .navbar-brand {
    font-size: 0.4rem !important;
  }

  .navbar_active {
    font-weight: bold;
    border-bottom: none;
  }

}

#header {
  width: 100%;
  margin-top: 10em;
}

#header h1 {
  margin: 0 0 10px 0;
  font-size: 72px;
  font-weight: 900;
  color: #0d1f35;
}

#header .band-name {
  color: #ff5203;
}

#header h2 {
  color: #0d1f35;
  font-size: 24px;
}

#header .btn-get-started {
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  background: #ff5203;
  color: #fff;
  border-radius: 2px solid #ff5203;
  text-decoration: none;
}

#header .btn-get-started:hover {
  background: #fff;
  color: #ff5203;
  font-weight: 900;
  border: 2px solid #ff5203;
}

.header-img {
  text-align: center;
}



@media only screen and (min-width: 960px) {

  #header .animated {
    animation: up-down 2s ease-in-out infinite alternate-reverse both;

  }
}


@-webkit-keyframes up-down {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }

  100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

@keyframes up-down {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }

  100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

#footer {
  background: chartreuse;
  background-color: #fdeae4;
}






/* /////// Responsive Media Queries ////// */

@media only screen and (max-width: 1180px) {
  .main-content-header {
    width: 100%;
    padding: 0 2%;
  }
}

@media only screen and (max-width: 998px) {
  h1 {
    font-size: 200%;
  }
}

@media only screen and (max-width: 768px) {
  h1 {
    font-size: 180%;
  }

  .main-nav {
    display: none;
    float: left;
  }

  .mobile-icon {
    display: inline-block;
    color: #fff;
    float: right;
    margin-top: 30px;
    margin-right: 20px;

  }

  .main-nav li {
    display: block;
    margin-top: 10px;
  }
}

@media only screen and (max-width: 480px) {
  .btn-full {
    margin-bottom: 20px;
  }

  h1 {
    font-size: 160%;
  }
}

footer ul {
  list-style-type: none;
}

footer a {
  text-decoration: none;
  color: unset;
}
